<template>
  <wm-dialog v-model:show="infoShow" :title="title" width="641px" bottom="32px" @close="close()">
    <wm-main>
      <wm-tabs :columns="tabs">
        <!--  -->
        <template #base>
          <wm-table-form>
            <tr>
              <td class="label"></td>
              <td colspan="3">
                <wm-cascader v-model:value="form.fid" :options="menusAll" bodyMinWidth="168px" clearable></wm-cascader>
              </td>
            </tr>
            <tr>
              <td class="label"></td>
              <td>
                <wm-input v-model:value="form.title" placeholder="" maxlength="16"></wm-input>
              </td>
              <td class="label"></td>
              <td>
                <wm-input v-model:value="form.en" placeholder="" maxlength="16"></wm-input>
              </td>
            </tr>
            <tr>
              <td class="label"></td>
              <td colspan="3">
                <wm-input v-model:value="form.url" placeholder="" maxlength="32"></wm-input>
              </td>
            </tr>
            <tr>
              <td class="label"></td>
              <td colspan="3">
                <wm-input v-model:value="form.controller" placeholder="" maxlength="32"></wm-input>
              </td>
            </tr>
            <tr>
              <td class="label"></td>
              <td>
                <wm-input v-model:value="form.ico" placeholder="" maxlength="32"></wm-input>
              </td>
              <td class="label"></td>
              <td>
                <wm-input v-model:value="form.sort" placeholder=", : 0, 1, 2" maxlength="32"></wm-input>
              </td>
            </tr>
            <tr>
              <td class="label"></td>
              <td colspan="3">
                <wm-input v-model:value="form.controller" type="textarea" :height="'80px'" placeholder="" maxlength="32"></wm-input>
              </td>
            </tr>
          </wm-table-form>
        </template>
        <!--  End -->
        <!--  -->
        <template #action>
          <wm-table-form>
            <tr class="title">
              <td></td>
              <td></td>
              <td></td>
              <td class="action">
                <wm-button effect="text" type="primary" @click="actionAdd()"></wm-button>
              </td>
            </tr>
            <tr v-for="(v,k) in form.action" :key="k">
              <td>
                <wm-input v-model:value="v.name" maxlength="32"></wm-input>
              </td>
              <td>
                <wm-input v-model:value="v.action" maxlength="32"></wm-input>
              </td>
              <td>
                <wm-input v-model:value="v.perm" maxlength="32"></wm-input>
              </td>
              <td class="action">
                <wm-button effect="text" type="danger" @click="actionRemove(k)"></wm-button>
              </td>
            </tr>
          </wm-table-form>
        </template>
        <!--  End -->
      </wm-tabs>
    </wm-main>
    <template #bottom>
      <wm-button effect="dark" type="primary" @click="submit()"> </wm-button>
    </template>
  </wm-dialog>
</template>

<style lang="less" scoped>
.wm-table_form .label{width: 70px;}
.wm-table_form .action{width: 60px; text-align: center;}
</style>

<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
/* UI */
import Ui from '@/library/ui'
import Request from '@/library/request'
import Time from '@/library/time'
/*  */
import wmMain from '@/components/container/main.vue'
import wmDialog from '@/components/dialog/index.vue'
import wmInput from '@/components/form/input/index.vue'
import wmButton from '@/components/form/button/index.vue'
import wmCascader from '@/components/form/cascader/index.vue'
import wmTableForm from '@/components/table/form.vue'
import wmTabs from '@/components/tabs/index.vue'

@Options({
  components: { wmMain, wmDialog, wmInput, wmButton, wmCascader, wmTableForm, wmTabs },
  props: {
    show: {type: Boolean, default: false},        // 
    title: {type: String, default: ''},   // 
  }
})
export default class MenusAdd extends Vue {
  // 
  show!: boolean;
  title!: string;
  // 
  store: any = useStore();
  state: any = this.store.state;
  // 
  infoShow: boolean = false;
  tabs: Array<any> = [
    {label: '', value: 'base', slot: 'base', checked: true},
    {label: '', value: 'action', slot: 'action'},
  ];
  form: any = {fid: [], title: '', en: '', ico: '', sort: 0, url: '', controller: '', action:[], remark:''}
  menusAll: any = [
  {label:'', value:'m1', checked: false, children: [
    {label:' 1-1', value:'m1_1', checked: false},
    {label:' 1-2', value:'m1_2', checked: false, children:[
      {label:' 1-2-1', value:'m1_2_1', checked: false},
      {label:' 1-2-2', value:'m1_2_2', checked: false},
    ]},
  ]},
  {label:'', value:'m2', checked: false, children:[
    {label:' 2-1', value:'m2_1', checked: false},
    {label:' 2-2', value:'m2_2', checked: false},
  ]},
  ];

  /*  */
  created(): void {
    this.$watch('show', (val:boolean)=>{
      this.infoShow = val;
    }, { deep: true });
  }
  /*  */
  public mounted(): void {
    this.form.fid = ['m2', 'm2_2'];
  }

  /* - */
  actionAdd(): void {
    const action: Array<any> = this.form.action;
    if(action.length>0) action.push({name:'', action:'', perm:action[action.length-1].perm*2});
    else this.form.action = [{name:'', action:'', perm:1}];
  }
  /* - */
  actionRemove(k: number): void {
    const action: Array<any> = this.form.action;
      action.splice(k, 1);
  }

  /*  */
  close(): void {
    this.$emit('update:show', false);
  }

  /*  */
  verify(form: any): any {
    if(!form.title || form.title.length<2) return Ui.Toast('2');
    return form;
  }

  /*  */
  submit(): void {
    // 
    const form = this.verify(this.form);
    if(!form) return;
    console.log(form);
  }

}
</script>